@import "https://fonts.googleapis.com/css2?family=Raleway&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
}

body {
  font-family: Raleway, sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  background-color: inherit;
  border: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  min-height: 70px;
  border-bottom: 1px solid #c3c3c3;
  box-shadow: 0 1px 5px #c3c3c3;
}

.header__menu {
  min-height: 70px;
  justify-content: flex-end;
  align-items: center;
  display: flex;
  position: relative;
}

.header__menu-link {
  text-transform: lowercase;
  opacity: .6;
  margin-left: 10%;
  font-size: 20px;
  font-weight: 800;
  position: relative;
}

.header__menu-link:after {
  content: "";
  height: 2px;
  width: 0;
  opacity: .6;
  background-color: #000;
  transition: width .3s ease-out;
  display: inline-block;
  position: absolute;
  top: 30px;
  right: 0;
}

.header__menu-link:hover:after {
  width: 100%;
}

@media screen and (max-width: 680px) {
  .header__menu-link {
    display: none;
  }
}

.menu-toggle {
  z-index: 3;
  width: 2.5rem;
  height: 2rem;
  background-color: #fff;
  display: none;
  position: relative;
}

@media screen and (max-width: 680px) {
  .menu-toggle {
    display: inline-block;
  }
}

.menu-toggle__item {
  width: 100%;
  height: .1rem;
  opacity: .6;
  background: #000;
  transition: all .3s;
  position: absolute;
  top: .9rem;
  left: 0;
}

.menu-toggle:before, .menu-toggle:after {
  content: "";
  width: 100%;
  height: .1rem;
  opacity: .6;
  background: #000;
  transition: all .3s;
  position: absolute;
  left: 0;
}

.menu-toggle:before {
  top: 0;
}

.menu-toggle:after {
  bottom: 0;
}

.menu-toggle.active:before {
  top: .9rem;
  transform: rotate(45deg);
}

.menu-toggle.active:after {
  bottom: .9rem;
  transform: rotate(-45deg);
}

.menu-toggle.active span {
  transform: scale(0);
}

